<template>
  <div>
     <p><button type="button" @click="one()">클릭이벤트</button></p>
     <hr>
     <p><button type="button" @click="one(), two()">여러개 함수 호출</button></p>
     <hr>
     <p><button type="button" @click="increaseCouter">Add 1</button></p>
     <p>The counter is :{{ counter }}</p>

 <!-- 파라미터 전달하고 싶을때 -->

     <button type="button" @click="setCount(7)">Set 7</button>
     <p>카운트는 :{{ count }}</p>
  </div>

</template>

<script>
//클릭 이벤트
//v-on:click or @click="메소드명"

export default {
  data() {
    return {
    counter:0,
    count:0
};
},

methods:{
   setCount(count) {
    this.counter=count;
  },
  one() {
    alert('원 입니다');
  },
  two() {
    alert('투 입니다');
  },
},
};
</script>

